.overlay {
    position: fixed;
    display: none;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.6); 
    z-index: 2;
    cursor: pointer;
  }

.overlay.win{
    display: block;
}

.overlay.lost{
    display: block;
}

#endcard{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    font-size: 24px;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 600;
    color: white;
    background-color: rgb(121, 172, 49);
    border-top: 5px solid rgb(168, 235, 74);
    border-left: 5px solid rgb(148, 211, 59);
    border-right: 5px solid rgb(94, 134, 38);
    border-bottom: 5px solid rgb(80, 116, 31);
    padding: 20px 40px 20px 40px;
}